.header {
  margin: auto;
  max-width: 880px;
  padding: 45px 0 0 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 880px) {
  .header {
    max-width: 90%;
  }
}

@media screen and (max-width: 320px) {
  .header {
    max-width: 320px;
    padding: 28px 0 0 0;
  }
}
