.form__container {
  position: relative;
  width: 430px;
  background: #fff;
  box-shadow: 0px 0px 25px rgb(0 0 0 / 15%);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .form__container {
    width: 84%;
  }
}

@media screen and (max-width: 320px) {
  .form__container {
    width: 282px;
  }
}
